import React, { createContext, useReducer } from "react";
import AppReducer from "./AppReducer";
import axios from "axios";

// Initial state
const initialState = {
    recipes: [],
    error: null,
    loading: true,
};

// Create context
export const GlobalContext = createContext(initialState);

// Provider component
export const GlobalProvider = ({ children }) => {
    const [state, dispatch] = useReducer(AppReducer, initialState);

    //
    async function getRecipes() {
        try {
            const res = await axios.get("/api/v1/recipes");
            dispatch({
                type: "GET_RECIPES",
                payload: res.data.data,
            });
        } catch (error) {
            dispatch({
                type: "RECIPE_ERROR",
                payload: error.response.data.error,
            });
        }
    }

    // Recipe Actions
    async function deleteRecipe(id) {
        try {
            await axios.delete(`/api/v1/recipes/${id}`);
            dispatch({
                type: "DELETE_RECIPE",
                payload: id,
            });
        } catch (error) {
            dispatch({
                type: "RECIPE_ERROR",
                payload: error.response.data.error,
            });
        }
    }

    async function addRecipe(recipe) {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };
        try {
            const res = await axios.post("/api/v1/recipes", recipe, config);
            dispatch({
                type: "ADD_RECIPE",
                payload: res.data.data,
            });
        } catch (error) {
            dispatch({
                type: "RECIPE_ERROR",
                payload: error.response.data.error,
            });
        }
    }

    // Ingredient Actions
    function deleteRecipeIngredient(recipeId, ingredientIndex) {
        dispatch({
            type: "DELETE_RECIPE_INGREDIENT",
            payload: [recipeId, ingredientIndex],
        });
    }

    function addRecipeIngredient(recipeId, ingredient) {
        dispatch({
            type: "ADD_RECIPE_INGREDIENT",
            payload: [recipeId, ingredient],
        });
    }

    return (
        <GlobalContext.Provider
            value={{
                recipes: state.recipes,
                error: state.error,
                loading: state.loading,
                getRecipes,
                deleteRecipe,
                addRecipe,
                deleteRecipeIngredient,
                addRecipeIngredient,
            }}
        >
            {children}
        </GlobalContext.Provider>
    );
};
